@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
    background-color: #d8f9ff;
    text-align: center;
    padding: 15px;
}

.header{
    background-color: #131e32;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.header-menu{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.header-link{
    text-decoration: none;
}

.main{
    background-image: url(/static/media/ng.8df8e7fa.jpg);
    min-height: calc(100vh - 70px);
}
.main-images{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.staze{
    background-image: url(/static/media/staze.2b304529.jpg);
    margin-top: 3rem;
    margin-bottom: 3rem;
    text-align: center;
    width: 35vw;
    height: 40vh;
    background-position: center;
    background-size: cover;
    border-radius: 10px;
    min-width: 350px;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.domovi{
    background-image: url(/static/media/domove.296cff2d.jpg);
    border-image-width: auto;
    text-align: center;
    background-position: center;
    background-size: cover;

    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.55));

            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.55));

    width: 35vw;
    height: 40vh;
    min-width: 350px;
    border-radius: 10px;
}
.pretraga{
    text-decoration: none;
    font-size: x-large;
    position: relative;
    top: 50%;
    left: 1%;
    background-color: white;
    padding: 5px;
    border-radius: 10px;
    color: #0b041f;
    opacity: 1;

}
.staze:hover, .domovi:hover{
    transform: scale(1.05);
}

@media all and (max-width: 600px){
    .domovi {
        width: 250px;
        height: 250px;
        min-width: 200px;
    }

    .staze {
        width: 250px;
        height: 250px;
        min-width: 200px;
        margin-top: 5vh;
    }

    .main-images {
        justify-content: space-around;
    }

    .pretraga {
        font-size: 1.2rem;
    }
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    background: #211648;
    color: white;

}

.title-container{
    display: flex;
    flex-direction: row;
    height: 70px;
    margin-left: 3rem;
    align-items: center;
    align-content: center;
}

.title{
    text-align: center;
    font-family: 'Lobster', cursive;
    color: white;
    padding-left: 1.5rem;
    font-size: 2rem;
}

.logo-image {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    background-size: cover;
    object-fit: cover;
}

.profil-image {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    background-size: cover;
    object-fit: cover;
