    text-align: center;
}


.loginAndRegisterButton, .logout-button {
    margin: auto 0.2rem;
    background-color: white;
    border: 2px solid white;
    height: 2rem;
    font-weight: 400;
    font-family: Roboto, sans-serif;
    border-radius: 7px;
    outline: none;
    color: #211648;
    background-color: white;
}

.logout-button {
    margin-right: 1rem;
}

.loginAndRegisterButton:hover, .logout-button:hover {
